@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,700);
.loader {
  align-items: center;
  justify-content: center;
  position: fixed;
  width: 100%;
  height: 100%;
  display: flex; }

@-webkit-keyframes spin {
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes spin {
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.spinner {
  border: 2px solid rgba(255, 255, 255, 0.1);
  border-left-color: #ffffff;
  border-radius: 50%;
  width: 80px;
  height: 80px;
  -webkit-animation: spin 1.2s linear infinite;
  animation: spin 1.2s linear infinite; }

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

html,
body {
  height: 100%; }

body {
  margin: 0;
  font-family: "Open Sans", sans-serif;
  font-size: 1rem;
  font-weight: 100;
  line-height: 1.5;
  background: #094c72;
  background: -webkit-radial-gradient(bottom, ellipse cover, #094c72 0%, #01293f 100%);
  background: -webkit-radial-gradient(ellipse at bottom, #094c72 0%, #01293f 100%);
  background: -webkit-radial-gradient(bottom, ellipse, #094c72 0%, #01293f 100%);
  background: radial-gradient(ellipse at bottom, #094c72 0%, #01293f 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#094c72', endColorstr='#01293f',GradientType=1 ); }

#root {
  height: 100%; }

.card {
  width: 892px;
  height: auto;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  display: flex;
  align-items: center; }
  .card .bg-sw {
    width: 480px;
    height: 553px;
    background: #cc3843;
    border-radius: 23px;
    display: flex;
    align-items: center; }
    .card .bg-sw .wordmark {
      opacity: 0.2;
      -webkit-transform: rotate(-90deg) scale(1.2);
              transform: rotate(-90deg) scale(1.2);
      margin-left: -90px; }
  .card .planet-info {
    width: 654px;
    height: 479px;
    background: #0c1f2c;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    left: -10%;
    -webkit-transform: translateX(50%);
            transform: translateX(50%);
    border-radius: 23px; }
    .card .planet-info .helmet {
      position: absolute;
      top: 50%;
      -webkit-transform: translate(-50%, -50%);
              transform: translate(-50%, -50%);
      left: -7%; }
    .card .planet-info .description {
      color: #fff;
      max-width: 390px;
      word-break: break-word; }
      .card .planet-info .description .name {
        font-weight: 800;
        line-height: 46px;
        font-size: 40px;
        margin-bottom: 40px;
        text-transform: uppercase; }
      .card .planet-info .description .info {
        margin-bottom: 40px;
        font-size: 20px; }
        .card .planet-info .description .info dt,
        .card .planet-info .description .info dd {
          display: inline; }
        .card .planet-info .description .info dt {
          margin-right: 10px; }
        .card .planet-info .description .info dd {
          text-transform: capitalize; }
          .card .planet-info .description .info dd:after {
            content: "";
            display: table;
            clear: both; }
        .card .planet-info .description .info.text-blur dd,
        .card .planet-info .description .info.text-blur span {
          text-shadow: 0 0 15px white;
          color: transparent; }

@media screen and (max-width: 576px) {
  .card {
    -webkit-transform: rotate(90deg) scale(0.7) translate(calc(50% - 25vh), 50%);
            transform: rotate(90deg) scale(0.7) translate(calc(50% - 25vh), 50%);
    position: static; } }

