    border: 1px solid white; }
    .sw-btn.sw-btn-outline:hover {
      background: #03080c;
      -webkit-transition: all 0.3s ease;
      transition: all 0.3s ease; }
    .sw-btn.sw-btn-outline:active {
      background: black; }

